.nile-table {
  width: 100%;
}

.nile-table th,
.nile-table td {
  font-size: 0.95rem;
}

.nile-table th {
  padding: 15px;
  font-weight: 600;
}

.nile-table td {
  font-weight: 300;
  padding: 10px 0;
}

.nile-table .minimal-btn {
  background-color: transparent;
  border: none;
  color: var(--black);
  padding: 0 12px;
}

.nile-table .minimal-btn:hover {
  cursor: pointer;
}

.nile-table .minimal-btn:disabled {
  cursor: unset;
  opacity: .2;
}

.nile-table .minimal-btn i {
  font-size: 24px;
}
